.headerWrap {
  padding: 14px 20px;
  background-color: var(--bgHeader);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @include minMd() {
    padding: 18px 32px;
  }
}

.headerTextWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.headerUserInfoWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.headerUserName {
  color: var(--modalTitle);
  font-family: map-get($fonts, m);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.28px;
  margin-right: 14px;
}


.headerAvatar {
  width: 32px;
  height: 32px;
  border-radius: 8px;  
  scale: 1;
  transition: scale 350ms;
}

.headerBtnAvatar {
  border: none;
  outline: none;
  width: 30px;
  height: 30px;
  color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  filter: drop-shadow(0px 0px 5px var(--accent));
  margin-left: 5px;

}

.headerAvatar:hover,
.headerAvatar:focus {
  scale: 1.07;
  transition: scale 350ms;
}



.svgThemeOpen {
  stroke: var(--colorFilters);
  stroke-width: 1.5px;
  fill: none;
  margin-left: 4px;
  margin-right: 14px;
  display: flex;
  flex-direction: row;
  align-items: center;

}


.btnThemeOpen {
  border: none;
  background: transparent;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}


.svgSideBarOpen {
  stroke: var(--modalTitle);

  @include minMd() {
    width: 34px;
    height: 34px;
  }
  @include minLg() {
    display: none;
  }
}

.btnSideBarOpen {
  border: none;
  background: transparent;
}

.themeText {
  color: var(--colorFilters);
  font-family: map-get($fonts, m);
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.28px;

}


.dropdownThemeWrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  position: relative;
}


.themeHeaderWrap {
  display: flex;
  flex-direction: row;
}

.dropdownThemeMenu {
  background-color: var(--bgModal);
  width: 100px;
  height: 107px;
  text-align: center;
  border-radius: 8px;
  border: 1px solid var(--borderTheme);
  box-shadow: 0px 4px 16px 0px rgba(17, 17, 17, 0.1);
  padding: 18px 44px 18px 18px;
  display: flex;
  justify-content: left;
  align-items: flex-start;
  position: absolute;
  top: 100%;
  left: -10%;
  z-index: 999999;
  animation: show 0.45s cubic-bezier(0, 0.1, 0.2, 1) 1;

}

@keyframes show {
0% {
  opacity: 0;
}
100% {
  opacity: 1;
}
}

.dropdownThemeList {
  cursor: pointer;
}

.dropdownThemeList:not(:last-child) {
  padding-bottom: 4px;
}

.themeMenuItem {
  color: var(--textThemeMenu);
  font-family: map-get($fonts, r);
  font-size: 14px;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.28px;
}

.themeMenuItem:hover,
.themeMenuItem:focus {
  color: var(--accent);
}
